*{
  padding: 0;
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{
  overflow-x: hidden;
}
.Navbar{
  width: 96vw;
  justify-content: space-between;
  align-items: center;
  display: flex;
  height: 30px;
  color: white;
  padding: 30px;
  background: none;
  position: fixed;
  z-index:100;
  background-color: #4887af93;
  
}
ul{
  list-style: none;
  display: flex;
}
li{
  display: block;
}
.Navbar .detail{
  width: 40vw;
  justify-content: space-between;
  align-items: center;
  color: blue;
}
.Navbar .title{
  font-weight: 600;
  font-size: 30px;
  color: red;
}
.Navbar .title .sm{
  font-size: 10px;
  color: blue;
}
.detail li{
  display: flex;
  align-items: center;
  margin-left: 70px;
  font-size: 12px;
}
.detail li:nth-child(1){
  font-size: 17px;
}
.MuiButtonBase-root{
  background: white;
}
.Navbar .MuiSvgIcon-root{
  color: red;
}
.top{
  height: calc(100vh - 90px);;
  width: 100vw;
  background-image: url(./assets/back.jpg);
  padding-top: 90px;
  background-size: contain;
  background-repeat: none;
  display: flex;
}
.service-detail .details{
  z-index: 100;
  background-color: #2684B5;
  height: 20vh;
  width: 100%;
  align-self:flex-end;
  font-size: 25px;
  font-weight: 600;
  color: white;
  bottom: 30vh;
}
.service-detail .details{
  display: flex;
  justify-content: space-around;
  text-align: center;
  align-items: center;
}
.service-detail .details div{
  display: flex;
  align-content: center;
  border-right: 1px solid white;
  padding-right: 5vw;
}
.service-about{
   display: flex;
   justify-content: center;

}
.service-about .about{
  width: 50vw;
  align-self: center;
  border-radius: 10px;
  filter: drop-shadow(0 0 0.75rem gray);
  background-color: white;
  margin-top: 30px;
 
  
}
.service-about .about .detail{
  padding: 20px;
}
.service-about .about .title{
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  text-align: center;
  background-color: #2B569A;
  color: white;
  padding: 15px;
  font-size: 22px;
  font-weight: 500;
}
.details button{
  width: 100%;
  padding: 8px;
  align-self: center;
  border-radius: 6px;
  background: none;
  border: 1px solid #2B569A;
  background-color: white;
  color: #2B569A;
  font-size: 15px;
  cursor: pointer;
}
.details button:hover{
  color: white;
  background-color: #2B569A;
}
.doctor{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-color: #6e98b981;
  width: 50%;
}
.appoint{
  width: 40%;
  align-self: center;
  padding-left: 70px;
  color: white;
  padding-right:140px;
  background-color: #6e98b981;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.appoint .title{
  font-size: 60px;
  font-weight: 600;
}
.doctor img{
  width: 60%;
}
.appoint .detail{
  font-size: 25px;
}
.appoint .button{
  background: linear-gradient(to left, white 50%, #2684B5 50%) right;
  color: black;
  background-size: 200%;
  width: 65%;
  border-radius: 30px;
  font-size: 20px;
  font-weight: 400;
  margin-top: 30px;
  padding: 10px;
  padding-left: 30px;
  padding-right: 30px;
  transition: 0.5s ease-out;
 display: flex;
 align-items: center;
 justify-content: center;
 text-align: center;
}
.appoint .button:hover{
 background-position: left;
 color: rgb(122, 112, 112);
}
.appoint .button a img{
  width: 10%;
}
.appoint .button a{
  display: flex;
  width:75%;
  justify-content: space-between;

}
.top .appoint .logo img{
  width:40%;
}
.top .appoint .logo{
  display: flex;
 margin-top: 30px;
}
/* .appoint .button a{
  display: flex;
  justify-content: space-between;
  width: fit-content;
} */
.cards{
  display: flex;
  justify-content: center;
}
.cards .card:nth-child(even){
  background-color: #2A93C9;
 
 
}
.cards .card:nth-child(odd){
  background-color: #2684B5;
}
.card{
  z-index: 2;
  top:10;
  height: 35vh;
  position: relative;
  color: white;
  padding: 30px;
  width: 18vw;
  font-size: 13px;
  font-weight: 500;
  bottom:10px;
  -webkit-transition:all 1s ease;
  -moz-transition:all 1s ease;
   -ms-transition:all 1s ease;
    -o-transition:all 1s ease;
       transition:all 1s ease;
}
.card:hover{
  bottom: 50px;
}
.card .title{
  font-weight: 600;
  font-size: 22px;
  margin-bottom: 40px;
}
.card ul{
  display: block;
}
.card ul li{
  display: block;
  display: flex;
  justify-content: space-between;
}
.test{
  display: flex;
  justify-content: space-around;
  margin-top: 70px;
}
.test-card{
  width: 25vw;
}
.test-card .title{
  font-size: 25px;
  font-weight: 600;
}
.partner{
  padding: 40px;
}
.partner .title{
  font-size: 25px;
  margin-bottom: 20px;
  font-weight: 600;
}
.partner .detail{
  display: flex;
  justify-content: space-around;
}
.partner .detail .detail-card{
  text-align: center;
  width: 30vw;
  
}
.partner .detail .detail-card .image{
  
}
.partner .detail .detail-card img{
  width: 80%;

}
.footer{
  display: flex;
  background-color: rgb(42, 147, 201);
  padding: 30px;
  color: white;
  justify-content: space-around;
}
.footer .about{
  width: 30%;
}
.footer .about .title{
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 20px;
}
.footer .about .social .MuiSvgIcon-root{
  font-size: 50px;
  margin-top: 10px;
  margin-left: 30px;
}

.footer .contact{
  width: 30%;
  font-weight: 430;

}
.footer .contact .title{
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 15px;
}
.footer .contact p{
  margin-top: 10px;
  display: flex;
  align-items: center;

}
.footer .nueclear{
  width: 30%;
}
.partner .detail .detail-card:hover{
  opacity: 0.6;
}
.services-card .serivces{
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  margin-bottom: 10px;
  
  
}
.services-card{
   display: flex;
   margin-top: 20px;
   flex-direction: column;
  justify-content: space-around;
  margin-left: 80px;
   width:85vw;
}
.services-card .stitle{
  background-color: #EFEFEF;
  color: #2B569A;
  font-weight: 650;
  padding: 10px;
  font-size: 20px;
}
.service-card{
  cursor: pointer;
  width: 17vw;
  height:12vw;
  border: 1px solid lightgray;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.service-card .title{
  font-weight: 700;
  color: #2B569A;
 
}
.service-card .price{
  font-weight: 700;
}
.service-card button{
  width: 40%;
  padding: 8px;
  align-self: center;
  border-radius: 6px;
  background: none;
  border: 1px solid #2B569A;
  color: #2B569A;
  font-size: 15px;
  cursor: pointer;
}
.service-card button:hover{
  background: #2B569A;
  border: 1px solid #2B569A;
  color: white;
}
a{
  text-decoration: none;
  color:unset;
}
.small{
  display: none;
}
